@import url(https://api.tiles.mapbox.com/mapbox-gl-js/v1.12.0/mapbox-gl.css);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
.search{box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15);border-radius:0.3rem;padding:0.5rem;background-color:#f5f6fe}.search .search-bar{font-size:1.5rem;width:20rem;height:3.5rem;position:relative;overflow:hidden}.search .search-bar .search-input{width:100%;height:100%;padding-top:1rem;background-color:inherit;color:#595f6e;border:none;outline:none}.search .search-bar .search-input:focus+.city-label .label-content,.search .search-bar .search-input:valid+.city-label .label-content{transform:translateY(-100%);color:#5fa8d3;font-size:1.4rem}.search .search-bar .search-input:focus+.city-label::after,.search .search-bar .search-input:valid+.city-label::after{transform:translateX(0)}.search .search-bar .city-label{position:absolute;bottom:0;left:0;width:100%;height:100%;pointer-events:none;border-bottom:1px solid #666666}.search .search-bar .city-label::after{content:'';position:absolute;bottom:-3px;left:0;width:100%;height:100%;border-bottom:3px solid #5fa8d3;transform:translateX(-100%);transition:transform 0.3s ease}.search .search-bar .city-label .label-content{position:absolute;bottom:5px;left:0;color:#595f6e;transition:transform 0.3s ease}.search .search-result{position:absolute;max-height:10rem;width:20rem;overflow-y:auto;background-color:#f5f6fe;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15);border:solid 1px #dddddd;border-radius:0.3rem;margin-top:1rem;scrollbar-width:thin;scrollbar-color:#aaaaaa #dddddd;z-index:1}.search .search-result .city-data{height:2rem;display:flex;justify-content:space-between;align-items:center;color:#595f6e;text-decoration:none;padding:0 0.3rem 0 0.3rem;border-radius:0.3rem;cursor:pointer}.search .search-result .city-data h2{font-size:1rem;font-weight:300}.search .search-result .city-data h3{font-size:0.8rem;font-weight:300}.search .search-result .city-data:hover{background-color:#dddddd}.search .search-result:empty{display:none}.search .search-result::-webkit-scrollbar{width:5px}.search .search-result::-webkit-scrollbar-track{background-color:#dddddd;border-radius:2px}.search .search-result::-webkit-scrollbar-thumb{background-color:#aaaaaa;border-radius:2px}

.panel-wrapper{width:100%;height:100%;min-height:auto}.panel-wrapper .panel{box-shadow:0 0.5rem 1rem rgba(0,0,0,0.15);border-radius:0.3rem;padding:0.5rem;background-color:#f5f6fe;width:100%;height:100%;display:flex;flex-direction:column;align-items:center}.panel-wrapper .panel img{width:64px;height:auto}.panel-wrapper .panel p{font-weight:300}.panel-wrapper .panel .description{font-weight:300;font-size:1.7rem;text-align:center}.panel-wrapper .panel .column-wrapper{display:flex;flex-direction:column;align-items:center}.panel-wrapper .panel .row-wrapper{width:90%;display:flex;flex-direction:row;justify-content:space-between;margin-top:1rem}.panel-wrapper .panel .row-wrapper p{font-size:1.2rem}.large{grid-column:2/4}.large .choose-chart{width:100%;display:flex;justify-content:space-around}.large .choose-chart button{background-color:inherit;border:none;outline:none;cursor:pointer;color:#bbbbbb}.large .choose-chart button:hover{color:#595f6e}.large .choose-chart .active{color:#5fa8d3}.large .choose-chart .active:hover{color:#5fa8d3}.forecast{transition:transform 0.3s ease;cursor:pointer}.forecast:hover{transform:scale(1.03)}

.weather-wrapper{display:grid;place-items:center}.weather-wrapper .weather-page{font-size:1.4rem;width:100vw;height:90vh;padding:2rem;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-gap:2rem;place-items:center}

.toggle-wrapper{width:15rem;display:flex;justify-content:center;align-items:center}.toggle-wrapper h3{font-size:1.2rem;font-weight:300}.toggle-wrapper .switch{position:relative;display:inline-block;width:4rem;height:2rem;background-color:#eee;border-radius:0.5rem;margin:0 0.5rem 0 0.5rem;cursor:pointer}.toggle-wrapper .switch::after{content:'';position:absolute;width:1.9rem;height:1.9rem;border-radius:0.5rem;background-color:#5fa8d3;top:1px;left:1px;transition:all 0.3s}.toggle-wrapper .checkbox{display:none}.toggle-wrapper .checkbox:checked+.switch::after{left:2rem}

.map-wrapper{position:relative}.map-wrapper .map{position:absolute;top:4.5rem;right:0;height:20rem;width:30rem;background-color:#f5f6fe;border-radius:0.3rem;display:grid;place-items:center;z-index:2;opacity:0;pointer-events:none;transition:opacity 0.3s ease}.map-wrapper .map .map-container{height:90%;width:90%}.map-wrapper .visible{opacity:1;pointer-events:all}.map-wrapper .fas{font-size:2rem;color:#595f6e;margin:1rem;cursor:pointer;transition:transform 0.3s ease, color 0.2s ease}.map-wrapper .fas:hover{transform:scale(1.2);color:#5fa8d3}

.position-wrapper .fas{font-size:2rem;color:#595f6e;margin:1rem;cursor:pointer;transition:transform 0.3s ease, color 0.2s ease}.position-wrapper .fas:hover{transform:scale(1.2);color:#5fa8d3}

.location-wrapper{width:15rem;display:flex;justify-content:center;align-items:center}

.nav{height:10vh;width:100vw;display:flex;justify-content:space-around;align-items:center}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Lato', sans-serif;font-size:62.5%;background-color:#cccccc}

